import { AREA_COORDS } from '../constants/constants';
import { BGS_VARIANT, LIGHTS_VARIANT, TREES_VARIANT } from '../constants/game-consts';
import { templateBallCount } from './filterPage-main';
export var templateTreesVariant = function () {
    var container = document.createElement('div');
    container.classList.add('select-trees');
    TREES_VARIANT.forEach(function (item, index) {
        var div = document.createElement('div');
        div.classList.add('tree-item');
        div.style.backgroundImage = "url(".concat(TREES_VARIANT[index].image, ")");
        container.appendChild(div);
    });
    return container.outerHTML;
};
export var treesListeners = function (treeHandler, treeCurrentDb, treeLSHandler) {
    var trees = document.querySelectorAll('.tree-item');
    trees.forEach(function (item, index) {
        item.addEventListener('click', function () {
            trees.forEach(function (item) {
                item.classList.remove('active');
            });
            item.classList.add('active');
            treeCurrentDb(TREES_VARIANT[index]);
            treeLSHandler();
            treeHandler();
        });
    });
};
