import { LENGTH_FAVORITE_ARR } from '../constants/constants';
import db from '../services/dbService';
import ls from '../services/lsService';
import { TypeFilter, TypePage } from '../types/enums';
import audioViews from './audioController';
import FilterPage from '../views/filterPageViews';
import LayoutController from './layoutController';
var FilterPageController = /** @class */ (function () {
    function FilterPageController() {
        var _this = this;
        this.setFilters = function (filtertype, item) {
            db.setCurrentFilter(filtertype, item);
            ls.setFiltersLocalStorage(db.getActiveFilters());
        };
        this.favoriteHandler = function (item) {
            var favorites = ls.getFavoriteLocalStorage();
            if (favorites.length < LENGTH_FAVORITE_ARR || favorites.includes(item)) {
                ls.setFavoriteLocalStorage(item);
                db.setFavoriteToys(ls.getFavoriteLocalStorage());
            }
            else {
                _this.filterPage.renderModal();
            }
            _this.filterPage.renderCards(db.getFilteredCardsArr(), ls.getFavoriteLocalStorage(), _this.favoriteHandler);
        };
        this.filterHandler = function (filtertype, item) {
            _this.setFilters(filtertype, item);
            if (filtertype !== TypeFilter.search) {
                _this.drawFilterPage();
                _this.filterPage.drawSliders(_this.sliderHandler, db.getActiveFilters());
            }
            else {
                _this.filterPage.renderCards(db.getFilteredCardsArr(), ls.getFavoriteLocalStorage(), _this.favoriteHandler);
            }
        };
        this.sortHandler = function (sortType) {
            db.setCurrentSort(sortType);
            ls.setSortLocalStorage(sortType);
            _this.filterPage.renderCards(db.getFilteredCardsArr(), ls.getFavoriteLocalStorage(), _this.favoriteHandler);
        };
        this.sliderHandler = function (filtertype, value) {
            _this.setFilters(filtertype, value);
            _this.filterPage.renderCards(db.getFilteredCardsArr(), ls.getFavoriteLocalStorage(), _this.favoriteHandler);
        };
        this.drawFilterSliders = function () {
            _this.filterPage.drawSliders(_this.sliderHandler, db.getActiveFilters());
        };
        this.resetHandler = function () {
            _this.drawFilterPage();
            _this.drawFilterSliders();
            audioViews.stopPlay();
        };
        this.drawFilterPage = function () {
            _this.filterPage.renderFilterPage(_this.filterHandler, db.getActiveFilters(), _this.sortHandler, db.getActiveSort(), db.setStartedFilters, _this.resetHandler, ls.setResetLocal);
            LayoutController.renderHeader(TypePage.filter);
            _this.filterPage.setSearchListeners(_this.filterHandler);
            _this.filterPage.renderCards(db.getFilteredCardsArr(), ls.getFavoriteLocalStorage(), _this.favoriteHandler);
        };
        this.filterPage = new FilterPage();
    }
    return FilterPageController;
}());
export default new FilterPageController();
