import Root from '../views/rootView';
import MenuController from './menuController';
import GameController from './gamePageControler';
import db from '../services/dbService';
import ls from '../services/lsService';
import audioViews from './audioController';
import { createSnowFlake } from '../utils/snowflakes';
var LayoutController = /** @class */ (function () {
    function LayoutController() {
        var _this = this;
        this.snowHandler = function (value) {
            db.setCurrentSnow(value);
            ls.setCurrentStorageSnow(value);
        };
        this.renderHeader = function (page) {
            _this.root.drawHeader(MenuController.drawMenu, GameController.drawGamePage, MenuController.startHandler, page, db.getFavoriteToys.length, db.getActiveFilters(), createSnowFlake, ls.getIsPlayMusic(), audioViews.audioListner, _this.snowHandler, ls.getCurrentStorageSnow());
        };
        this.root = new Root();
    }
    return LayoutController;
}());
export default new LayoutController();
