import Game from '../views/gamePageViews';
import db from '../services/dbService';
import ls from '../services/lsService';
import audioViews from './audioController';
import LayoutController from './layoutController';
import { TypePage } from '../types/enums';
var GameController = /** @class */ (function () {
    function GameController() {
        var _this = this;
        this.treeHandler = function () {
            ls.setCurrentStorageTree(db.getCurrentTree());
        };
        this.bgHandler = function () {
            ls.setCurrentStorageBG(db.getCurrentBG());
        };
        this.lightHandler = function (light) {
            db.setCurrentLights(light);
            ls.setCurrentStorageLight(light);
        };
        this.renderTreeBlock = function () {
            _this.game.renderTree(db.getCurrentTree(), db.getCurrentLights());
        };
        this.cordHandler = function (num, indexCurrToy, value) {
            db.changeItemCoord(num, indexCurrToy, value);
            _this.game.renderToys(db.getFavoriteToys());
        };
        this.resetHandler = function () {
            ls.generateLocalStorage();
            LayoutController.renderHeader(TypePage.game);
            _this.drawGamePage();
            audioViews.stopPlay();
        };
        this.drawGamePage = function () {
            _this.game.renderGamePage(db.setCurrentTree, db.setCurrentBG, _this.treeHandler, _this.bgHandler, db.getCurrentBG(), _this.lightHandler, _this.renderTreeBlock, db.setStartedFilters, _this.resetHandler, ls.setResetLocal);
            LayoutController.renderHeader(TypePage.game);
            _this.game.renderToys(db.getFavoriteToys());
            _this.game.setGlobalDropEvent(_this.cordHandler);
            _this.game.renderCurrentSetting(db.getCurrentTree(), db.getCurrentLights(), db.getCurrentBG());
        };
        this.game = new Game();
    }
    return GameController;
}());
export default new GameController();
